@font-face {
  font-family: "Paragone";
  src: url(/paragone-luxury-serif-font/Paragone/Paragone.ttf);
}
@font-face {
  font-family: "BodyParagone";
  src: url(/paragone-luxury-serif-font/Paragone/Paragone.otf);
}

.navbar-logo {
  height: 50px;
  border-radius: 90px;
}
:root {
  --mainColor: transparent;
  /* --mainColorLight: #3b5249; */
  --text360screen:10px;
  --secondarColor: goldenrod;
  --textColor: #eee;
}
.header {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  height: 40px;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
  /* background-color: white; */
  padding: 10px;
}
.bars{
    position: absolute;
    top: 15px;
    right: 28px;
    transform: scale(2.9,1.5);
    font-size: 18px;
    margin-top: 8px;
    color: white;
    /* background-color: pink; */
}
.the{
margin-left: -800px;
padding-top: 13px;
font-size: 23px;
/* background-color: pink; */

}

nav a {
  margin: 0 2rem;
  color: var(--textColor);
  text-decoration: none;

}
nav a:hover {
  color: var(--secondarColor);
}
header .nav-btn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
}

@media only screen and (max-width: 1024px) {
  header .nav-btn {
    visibility: visible;
    opacity: 1;
  }
  header nav {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
    transform: translateY(-100vh);
  }

  header .responsive_nav {
    transform: none;
  }

  nav .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
  }
  nav a{
    font-size: 1.5rem;
  }
}
