@font-face {
    font-family: "Paragone";
    src: url(/paragone-luxury-serif-font/Paragone/Paragone.ttf);
  }
  @font-face {
    font-family: "BodyParagone";
    src: url(/paragone-luxury-serif-font/Paragone/Paragone.otf);
  }
  @font-face {
    font-family: "luxury";
    src: url(/din/fonts/FF\ DIN\ Pro\ Regular.otf);
  }
  .navbary-logo {
    height: 67px;
    border-radius: 90px;
    margin-bottom: 10px;
  }
  :root {
    --mainColor: transparent;
    --mainColorLight: #3b5249;
    --secondarColor: peachpuff;
    --textColor: #eee;
  }
  .headerr {
    display: flex;
    justify-content: space-between;
    height: 70px;
    padding: 0 2rem;
    /* background-color: pink; */
    padding: 10px;
  }
  .mtoto nav{
    /* background-color: pink; */
    height: 70px;
  }
  .barss{
      position: absolute;
      top: 15px;
      right: 28px;
      transform: scale(2.9,1.5);
      font-size: 18px;
      margin-top: 8px;
      color: black;
  }
  .menu{
    position: absolute;
      top: 27px;
      right: 98px;
      color: black;
    /* background-color: pink; */
  }
  .menutext{
    font-size: 17px;
    color: rgb(104, 99, 99);
    font-family: 'luxury';
  }
  .th{
  margin-left: -788px;
  margin-top: 29px;
  font-size: 25px;
  color: #7b7471;
  font-family: 'luxury';
  
  }
  
  nav a {
    margin: 0 2rem;
    color: var(--textColor);
    text-decoration: none;
  
  }
  nav a:hover {
    color: goldenrod;
  }
  header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
  }
  
  @media only screen and (max-width: 1024px) {
    header .nav-btn {
      visibility: visible;
      opacity: 1;
    }
    header nav {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      /* align-items: center; */
      justify-content: center;
      gap: 1.5rem;
      background-color: var(--mainColor);
      transition: 1s;
      transform: translateY(-100vh);
    }
  
    header .responsive_nav {
      transform: none;
    }
  
    nav .nav-close-btn {
      position: absolute;
      top: 2rem;
      right: 2rem;
    }
    nav a{
      font-size: 1.5rem;
    }
  }
  .touch{
    font-family: 'luxury';
    margin-right: 6px;
    margin-left: 6px;
    color: #7b7471;
  }